.navbar {
  display: flex;
  justify-content: space-around;
  background-color: brown;
  color: white;
  font-size: 24px;
  padding: 15px 10%;
}
.navbar a {
  text-decoration: none;
  color: white;
  text-transform: capitalize;
}
.navbar a:hover {
  color: yellow;
}

.footer {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
  background-color: brown;
  color: white;
  font-size: 12px;
}

.page404 {
  width: 100%;
  height: 75vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: red;
}

/* --------------------------------------------------- */
.loadingContainer {
  font-size: 36px;
  text-align: center;
}

.allProductsContainer {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  padding: 15px;
}

.onProductCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  padding: 15px;
  box-shadow: 2px 1px 5px rgb(183, 183, 183);
}

.productImage img {
  max-height: 250px;
}

.productTitle {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
}

.productPrice {
  text-align: center;
  color: darkgreen;
  font-size: 24px;
}

.onProductCard a {
  text-decoration: none;
}
.onProductCard a:hover {
  color: brown;
}

/* --------------------------------------------------- */

.product-details {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  gap: 10%;
  height: auto;
}

.product-img {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
}

.product-img img {
  width: 350px;
  height: 300px;
  object-fit: contain;
}

.product-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.title {
  font-size: 26px;
  font-weight: bold;
  color: rgb(86, 36, 221);
}

.title span {
  display: flex;
  flex-wrap: wrap;
  font-size: 30px;
  color: brown;
}

.desc {
  font-size: 20px;
  text-align: left;
  margin-bottom: 20px;
}

.desc span {
  color: cadetblue;
  display: flex;
  flex-wrap: wrap;
  font-size: 30px;
  font-weight: bold;
}

.last {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

.price,
.category {
  font-size: 30px;
}

.price span {
  color: green;
  font-weight: bold;
  margin-right: 10px;
}

.category span {
  color: red;
  font-weight: bold;
  margin-right: 10px;
}
